import {changeContent} from '../utils/change-content';

const showTabsOnLoad = () => {
  const container = document.querySelector('.benefit');

  if (container) {
    const hash = window.location.hash;

    if (!hash) {
      const firstTabButton = document.querySelectorAll('.js-button-tabs')[0];
      let firstId = firstTabButton.dataset.hash;

      changeContent('modal-' + firstId, '-picture', '.modal-picture', 'active-picture');
      changeContent('modal-' + firstId, '-list', '.modal-list', 'active-list');
      changeContent('modal-' + firstId, '-title', '.modal-title', 'active-title');

      window.location.hash = firstId;
    } else {
      const id = hash.substr(1);
      let btnId = 'benefit-' + id;
      let modalId = 'modal-' + id;

      const btn = document.getElementById(btnId);

      btn.setAttribute('checked', 'true');

      changeContent(modalId, '-picture', '.modal-picture', 'active-picture');
      changeContent(modalId, '-list', '.modal-list', 'active-list');
      changeContent(modalId, '-title', '.modal-title', 'active-title');
    }
  }
};

const showTabsClickButton = () => {
  const btn = document.querySelectorAll('.js-button-tabs');

  if (btn) {
    btn.forEach((item) => {
      item.addEventListener('click', (evt) => {
        const input = evt.target;
        const modalId = input.dataset.id;
        let hash = input.dataset.hash;

        changeContent(modalId, '-picture', '.modal-picture', 'active-picture');
        changeContent(modalId, '-list', '.modal-list', 'active-list');
        changeContent(modalId, '-title', '.modal-title', 'active-title');

        window.location.hash = hash;
      });
    });
  }
};

export {showTabsOnLoad, showTabsClickButton};
